export default ":host > button {\n  width: 34px;\n  height: 34px;\n  background: none;\n  border: none;\n  padding: 0;\n  margin: 0;\n  cursor: pointer;\n  position: relative;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  border-radius: 4px;\n\n  & svg {\n    width: 24px;\n    height: 24px;\n  }\n\n  &:not(.is-disabled):hover,\n  &.is-active {\n    background: #dee0e2;\n  }\n}\n\n:host > button.tooltip-disabled .mt-rich-text-editor-tooltip {\n  display: none;\n}\n\n:host > button:disabled *:not(.mt-rich-text-editor-tooltip) {\n  opacity: 0.5;\n  cursor: default;\n}\n"